import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import data from './some.json'


class Contacts extends Component {
  render() {
    return <React.Fragment>
      <li>
        <img
          src={this.props.picture}
          alt="IMG"
          className="img"/>
        <div className="card">
          <span className="name">{this.props.name}</span>
          <span className="phone">{this.props.phone}</span>
          <span className="address">{this.props.address}</span>
        </div>
      </li>
    </React.Fragment>
  }
}

class ContactsList extends Component {
  
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      displayedContacts: data
    };
    
    this.handleSearch = this.handleSearch.bind(this);
  }
  
  handleSearch(event) {
    let searchQuery = event.target.value.toLowerCase();
    let displayedContacts = data.filter(function (el) {
      let searchValue = el.name.toLowerCase();
      return searchValue.indexOf(searchQuery) !== -1;
    })
    
    this.setState({
      displayedContacts: displayedContacts
    })
    
  }
  
  render() {
    return (
      <div className='wrap'>
        <input
          type="text"
          onChange={this.handleSearch}
          
          placeholder=" "
        />
        <ul>
          {this.state.displayedContacts.map(function (el) {
            return <Contacts
              picture={el.picture}
              name={el.name}
              phone={el.phone}
              key={el._id}
              address={el.address}
            />
          })}
        </ul>
      </div>)
  }
}


ReactDOM.render(<ContactsList/>, document.getElementById('root'));




